
.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}
#content .presentIndex a,#content .presentIndex span{
  font-weight: bold;
  font-size: 12px;
  margin-bottom: 1%;
  color: #000;
  cursor: pointer;
}
/*进度条*/
#content .progressBar{
  margin-bottom: 5px;
  margin-top:10px;
  width:80%;
  margin-left:10%;
}
/*进度条标签*/
#content .progressBar-label{
  width:80%;
  margin-left:10%;
}
#content .progressBar-label li{
  list-style: none;
  float:left;
  width:20%;
  text-align: center;
  font-size: 13px;
}

/*同伴评估内容*/
#homework-peerAssess span{
  font-weight: bold;
}
#homework-peerAssess div{
  margin-top:10px;
}
/*同伴作业面板*/
#homework-peerAssess .panel-heading{
  margin-top:0px;
}
#homework-peerAssess  .panel-body{
  margin-top:0px;
}
/*分数输入框*/
#homework-peerAssess input,
#selfHomework input{
  width:100px;
  display: inline;
}

/*设置五星评分及文本*/
.peerHW-text-star-1,.peerHW-text-star-2,.peerHW-text-star-3{
  margin-left: 25px;
}
#homework-peerAssess img
{
  width:25px;
  height:25px;
  cursor: pointer;
}
#homework-peerAssess textarea{
       width:90%;
       margin-left: 5%;
       margin-top: 10px;
}

/*提交重置按钮*/
#peerAssessBtn button{
  width:100px;
  margin-bottom:20px;
  margin-left:25%;
}






