
.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}
/*同伴评估内容*/
#homework-modAssess span{
  font-weight: bold;
}
#homework-modAssess div{
  margin-top:10px;
}
/*评估主内容中的面板设置*/
#homework-modAssess .modHomework .panel-heading{
  margin-top:0px;
}
#homework-modAssess .modHomework .panel-body{
  margin-top:0px;
}
/*评估结果中的分数排名*/
#modHomework-result .modHomework-score{
  display: inline;
}
#modHomework-result .modHomework-score span{
      margin-right:30%;
}

/*评价列表*/
#modHomework-result .modHomework-text{
  margin-top:20px;
}

/*评价表格*/
#modHomework-table {
  width:95%;
  margin-top:10px;
  margin-left: 2.5%;
}
#modHomework-table .modHW-table-left{
  width:5%;
}
#modHomework-table .modHW-table-right{
  width:95%;
}


/*修改面板*/
#modHomework-mod .panel-heading,#modHomework-mod .panel-body{
  margin-top:0px;
}



/*评估结果图表*/
#assess-chart {
  width   : 90%;
  height    : 300px;
  font-size : 11px;
}








/*提交重置按钮*/
#modAssessBtn button{
  width:100px;
  margin-bottom:20px;
  margin-left:25%;
}







