
.topbar {
  border-bottom: solid #D5D5D5;
  background-color: #F5F5F5;
  width: 100%;
  height: 55px;
}
.logo {
  width: 60%;
  float: left;
}
.logo span {
  font-size: 25px;
  float: right;
}
#nav-right {
  width: 40%;
  float: left;
}
#nav-right li {
  float: right;
  list-style: none;
}
#nav-right .user-photo {
  padding: 10px;
}
#nav-right .user-photo img {
  width: 35px;
  height: 35px;
}
#nav-right .username {
  padding-left: 0px;
  font-weight: bold;
}
#nav-right .notice-icon img {
  width: 20px;
  height: 20px;
}
#nav-right .setup-icon img {
  width: 20px;
  height: 20px;
}




